<template lang="pug">
    .group__schedule-day 
        select(@change='onChange()' v-model="selectedTime").new__group-schedule-day
            option(:value='0') Выбрать
            option(:value='idx+1' v-for='item,idx in times' :selected='selectedTime==idx+1' :disabled='teacherSchedule.filter(ele=>ele.scheduleDay==day).length!=0 && teacherSchedule.filter(ele=>ele.scheduleDay==day && ele.scheduleTime == idx+1).length!=0').existed__group-schedule-option {{item}}
</template>
<script>
export default {
    data() {
        return {
            disabled: 0,
            times: [
                '08.00-09.00', '09.00-10.00', '10.00-11.00', '11.00-12.00', '12.00-13.00',
                '13.00-14.00', '14.00-15.00', '15.00-16.00', '16.00-17.00',
                '17.00-18.00'
            ]
        }
    },
    props: {
        selectedTime: Number,
        day: Number,
        index: Number,
        teacherSchedule: Array
    },
    methods: {
        onChange(){
            var id = (this.index - 1) * 10 + this.selectedTime
            const scheduleItem = {
                id: id,
                scheduleDay: this.index,
                scheduleTime: this.selectedTime
            }
            this.$emit('scheduleTimeSelect', scheduleItem)
        }
    }
} 
</script>
<style scoped lang="scss">
.new__group-schedule-day {
    background-color: #FFFAF0;
    border-radius: 5px;
    padding: 4px;
    width: 80%;
}
</style>